import TicketListItem from './TicketListItem';
    export default {
        data () {
            return {
                ticketList: [
                    // {
                    //     ID: '1423',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1421',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1422',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1419',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // }
                ],
                loading: false,
                key: 0
            }
        },
        computed: {
            wxCode () {
                return this.$route.query.code;
            }
        },
        components: {
            TicketListItem: TicketListItem
        },
        methods: {
            openTicketDetail: function (id) {
                console.log(id);
                this.$router.push('/TicketDetail/' + id);
            },
            load: function () {
                if (this.ticketList.length < 10) {
                    return false;
                }
                this.loading = true;
                this.key += 10;
                this.WXUserInfo.then((userInfo) => {
                    this.$axios({
                        method: 'post',
                        url: this.CONFIG.url.getTicketList,
                        data: {
                            key: this.key,
                            openID: userInfo.openid
                        }
                    }).then((res) => {
                        this.loading = false
                        if (res.data.length > 0) {
                            this.ticketList = this.ticketList.concat(res.data);
                        }
                    })
                })
            },
        },
        mounted() {
            var loading = this.$loading();
            this.WXUserInfo = this.common.getWXUserInfo(this.wxCode);
            this.WXUserInfo.then((userInfo) => {
                this.$axios({
                    method: 'post',
                    url: this.CONFIG.url.getTicketList,
                    data: {
                        key: this.key,
                        openID: userInfo.openid
                    }
                }).then((res) => {
                    loading.close();
                    if (res.status == 200) {
                        if (res.data.length == 0) {
                            this.$toast.error('No ticket found!');
                            return false;
                        } else {
                            this.ticketList = this.ticketList.concat(res.data);
                        }
                    }else {
                            this.$toast.error('Please bind Contact first!');
                    }
                }).catch((err) => {
                    loading.close();
                    this.$toast.error('No more ticket!');
                });
            })
        }
    }



